<template>
	<div class="login">
		<div class="signin">
			<div style="width: 138px; margin: 0px auto;">
				<svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px"
					y="0px" viewBox="0 0 200 66.92" style="enable-background: new 0 0 200 66.92;" xml:space="preserve">
					<image style="overflow: visible;" width="1772" height="506"
						transform="matrix(0.1071 0 0 0.1071 5.1307 5.6736)" xlink:href="@/assets/Definer_logo.png"></image>
				</svg>
			</div>
			<p style="color: black;padding-bottom:20px;">

			</p>

			<div class="table_wrapper" style="height: 212px;width:100%;">
				<el-table class="table" border :data="list" size="small" height="100%">
					<el-table-column :label="lang.table.no">
						<template slot-scope="scope">
							{{ scope.$index + 1 }}
						</template>
					</el-table-column>

					<el-table-column prop="name" :label="lang.table.name"></el-table-column>
					<el-table-column :label="lang.table.dost">
						<template slot-scope="scope">
							<el-tooltip class="item" effect="dark" placement="top">
								<el-button type="text" icon="el-icon-edit" @click="goEdit(scope.row.id)"
									style="background-color: blue; padding: 8px; border-radius: 8px;color:white;"></el-button>
							</el-tooltip>
							<el-tooltip class="item" effect="dark" placement="top">
								<el-button type="text" icon="el-icon-delete" @click="del(scope.row.name)"
									style="background-color: red; padding: 8px; border-radius: 8px;color:white;"></el-button>
							</el-tooltip>
						</template>
					</el-table-column>
				</el-table>
			</div>
			<el-button class="submit" type="primary" style="margin-top:30px;" :loading="submitting" @click="createTag">
				{{ lang.tips22 }}
			</el-button>
		</div>
	</div>
</template>
  
<script>
import timer from "../tools/timer";
import global from "../tools/global";
export default {
	name: "team",
	data() {
		return {
			key: "",
			list: [],
			count: 0,
			page: 1,
			loading: false,
			showAdd: false,
			editingRow: null,
			selectedId: null,
			preference_name: '',
			showDateTimePicker: false, // Track visibility of the DateTime picker
			selectedDateTime: "",

			selectedSubscription: "",
			addForm: {
				name: "",
				role: "2",
			},
		};
	},
	created() {
		this.lang = this.$lang("team");
	},
	mounted() {
		this.getList();
	},
	methods: {
		// Get data list
		getList() {
			this.loading = true;
			this.$ajax.get('/project/list_tags').then((result) => {
				this.list = result;
				this.loading = false;
			});
		},
		refreshList() {
			this.getList();
		},
		addNewPreference() {
			this.$router.push({ path: '/u.library', query: this.$route.query });
		},
		// 重命名
		goEdit(id) {
			let that = this;
			that.$prompt(this.lang.tips24, '', {
				inputPattern: /\S/,
				inputErrorMessage: that.lang.tips5,
				closeOnPressEscape: false,
				closeOnClickModal: false,
				cancelButtonText: this.lang.tips19,
				confirmButtonText: this.lang.tips20,
				beforeClose(action, instance, done) {
					if (action == 'confirm') {
						if (that.httping) return;
						that.httping = true;
						that.$ajax.post('/group/rename_tag', {
							id,
							name: instance.inputValue
						}).then(r => {
							that.$message.success('success');
							that.refreshList();
							done();
							setTimeout(() => {
								that.httping = false;
							}, 300)
						});
					} else {
						done();
					}
				}
			}).catch(() => { });
		},
		createTag() {
			let that = this;
			that.$prompt(this.lang.tips23, '', {
				inputPattern: /\S/,
				inputErrorMessage: that.lang.tips5,
				closeOnPressEscape: false,
				closeOnClickModal: false,
				cancelButtonText: this.lang.tips19,
				confirmButtonText: this.lang.tips20,
				beforeClose(action, instance, done) {
					if (action == 'confirm') {
						if (that.httping) return;
						that.httping = true;
						that.$ajax.post('/group/add_tag', {
							name: instance.inputValue
						}).then(r => {
							done();
							that.refreshList();
							that.$message.success('success');
							setTimeout(() => {
								that.httping = false;
							}, 300)
						});
					} else {
						done();
					}
				}
			}).catch(() => { });
		},
		// 删除项目
		del(name) {
			let that = this;
			this.$confirm(
				`${this.lang.tips21} '${name}'?`,
				' ',
				{
					closeOnPressEscape: false,
					closeOnClickModal: false,
					cancelButtonText: this.lang.tips19,
					confirmButtonText: this.lang.tips20,
					showCancelButton: true,
					showConfirmButton: true,
					customClass: 'custom-confirm-dialog',
					beforeClose(action, instance, done) {
						if (action == 'confirm') {
							if (that.httping) return;
							that.httping = true;
							let index = 0;
							that.list.some((item, i) => {
								if (item.name == name) {
									index = i;
									return true;
								}
							});
							that.$ajax
								.get('/group/del_tag_name/?name=' + name)
								.then(r => {
									that.list.splice(index, 1);
									that.$message.success('success');
									done();
									setTimeout(() => {
										that.httping = false;
									}, 300);
									// Update the user's projects and trigger computed property update
									// that.updateUserProjects();
								});
						} else {
							done();
						}
					}
				}
			).catch(() => { });
		}


	},
};
</script>
  

<style lang="less" scoped>
@import url('../tools/common.less');

.login {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	/* Horizontally center content */
	align-items: center;
	/* Vertically center content */
	position: absolute;
	background-color: #355CA1;
}

.signin {
	width: 512px;
	background-color: #FFFFFF;
	border-radius: 24px;
	padding: 30px;

	p {
		color: #86868B;
		text-align: center;
		margin-top: 12px;
	}

	.submit {
		width: 200px;
		margin: 10px auto;
		background-color: #345CA0;
		display: block;
	}
}
</style>
